var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"info base_width"},[_c('h3',{staticClass:"header font-weight-normal"},[_vm._v("基金募集机构投资者适当性管理实施指引（试行）")]),_c('h5',{staticClass:"tips font-weight-normal"}),_vm._m(0),_c('infoPage',{attrs:{"first":_vm.first,"last":_vm.last,"prevUrl":_vm.prevUrl,"nextUrl":_vm.nextUrl}})],1)}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"content"},[_c('div',{staticClass:"box-header"},[_c('p',{staticClass:"margins"},[_vm._v("第一章 总则")]),_c('p',{staticClass:"indent"},[_vm._v("第一条【立法依据】为规范基金募集机构销售行为，指导投资者适当性管理制度的有效落实，维护投资者合法权益，依据《证券投资基金法》、《证券期货投资者适当性管理办法》（以下简称《办法》）、《证券投资基金销售管理办法》、《证券投资基金销售适用性指意见》、《私募投资基金募集行为管理办法》及其他法律法规制定本指引。")]),_c('p',{staticClass:"indent"},[_vm._v("第二条【适用范围】基金募集机构向投资者公开或者非公开募集的证券投资基金和股权投资基金（包括创业投资基金）产品，或者为投资者提供相关业务服务的（以下统称基金品或者服务），适用本办法。基金募集机构是指公募基金管理人、私募基金管理人（以下统称基金管理人），在中国证监会注册取得基金销售业务资格并已成为中国证券投资基金业协会（以下简称协会）会员的机构。")]),_c('p',{staticClass:"indent"},[_vm._v("第三条【适当性定义】投资者适当性是指基金募集机构在销售基金产品或者服务的过程中，根据投资者的风险承受能力销售不同风险等级的基金产品或者服务，把合适的基金产品或者服务卖给合适的投资者。 本指引所称的专业投资者，为符合本指引第二十二条规定的投资者；普通投资者，为符合本指引第二十六条规定的投资者；风险承受能力最低类别投资者，为符合本指引第二十九条规定的投资者。")]),_c('p',{staticClass:"indent"},[_vm._v("第四条【基金募集机构义务】基金募集机构应当按照本指引，建立健全投资者适当性管理制度。在销售基金产品或者服务过程中，应当勤勉尽责，诚实信用深入调查分析基金管理人、基金产品或者服务及投资者信息，充分揭示基金产品或者服务风险，降低错配而导致的投资者投诉风险。")]),_c('p',{staticClass:"indent"},[_vm._v("第五条【自律管理】协会依据法律法规和自律规则，对基金募集机构投资者适当性制度建立及实施情况进行自律管理。")]),_c('p',{staticClass:"margins"},[_vm._v("第二章 一般规定")]),_c('p',{staticClass:"indent"},[_vm._v("第六条【指导原则】基金募集机构在实施投资者适当性的过程中应当遵循以下指导原则：")]),_c('p',{staticClass:"indent"},[_vm._v("（一）投资者利益优先原则。当基金募集机构或基金销售人员的利益与投资者的利益发生冲突时，应当优先保障投资者的合法利益。")]),_c('p',{staticClass:"indent"},[_vm._v("（二）全面性原则。基金募集机构应当将投资者适当性管理作为内部控制的组成部分，将适当性管理贯穿于基金销售的各个业务环节，并覆盖销售的全部基金产品或者服务。")]),_c('p',{staticClass:"indent"},[_vm._v("（三）客观性原则。基金募集机构应当建立科学合理的方法，设置必要的标准和流程，保证适当性管理的实施。对基金管理人、基金产品或者服务和投资者的调查和评价，应当尽力做到客观准确，并作为基金销售人员向投资者推介合适基金产品或者服务的重要依据。")]),_c('p',{staticClass:"indent"},[_vm._v("（四）及时性原则。投资者的风险承受能力评价应当至少每两年更新一次，基金募集机构逾期再次向投资者推介基金产品或者服务时，需重新对投资者进行风险承受能力评价；基金产品或者服务的风险分级应当根据实际情况及时更新。")]),_c('p',{staticClass:"indent"},[_vm._v("（五）有效性原则。通过建立科学的投资者适当性管理制度与方法，确保投资者适当性管理的有效执行。")]),_c('p',{staticClass:"indent"},[_vm._v("（六）差异性原则。基金募集机构应当对投资者进行分类管理，对普通投资者和专业投资者实施差别适当性管理，履行差别适当性义务。")]),_c('p',{staticClass:"indent"},[_vm._v("第七条【管理制度】基金募集机构建立适当性管理制度，应当至少包括以下内容：")]),_c('p',{staticClass:"indent"},[_vm._v("（一）对基金管理人进行审慎调查的方式和方法；")]),_c('p',{staticClass:"indent"},[_vm._v("（二）对基金产品或者服务的风险等级进行设置、对基金产品或者服务进行风险评价的方式或方法；")]),_c('p',{staticClass:"indent"},[_vm._v("（三）对投资者进行分类的方法和程序、投资者转化的方法和程序；")]),_c('p',{staticClass:"indent"},[_vm._v("（四）对普通投资者风险承受能力进行调查和评价的方式和方法；")]),_c('p',{staticClass:"indent"},[_vm._v("（五）对基金产品或者服务和投资者进行匹配的方法；")]),_c('p',{staticClass:"indent"},[_vm._v("（六）投资者适当性管理的保障措施和风控制度。")]),_c('p',{staticClass:"indent"},[_vm._v("第八条【审慎调查】基金募集机构选择销售基金产品或者服务，应当对基金管理人进行审慎调查并做出评价，了解基金管理人的诚信状况、经营管理能力、投资管理能力和内部控制情况，并可将调查结果作为是否销售该基金管理人产品或者服务、是否向投资者推介该基金管理人的重要依据。基金管理人在选择基金募集机构时，为确保适当性的贯彻实施，应当对基金募集机构进行审慎调查，了解基金募集机构的内部控制情况、信息管理平台建设、账户管理制度、销售人员能力和持续营销能力，并可将调查结果作为选择基金募集机构的重要依据。")]),_c('p',{staticClass:"indent"},[_vm._v("第九条【人员管理】基金募集机构应当建立对销售人员的考核、监督问责、培训等机制规范销售人员履行投资者适当性工作职责的情况。基金募集机构不得采取鼓励其向投资者销售不适当基金产品或者服务的考核、激励机制或措施。")]),_c('p',{staticClass:"indent"},[_vm._v("第十条【人员管理】基金募集机构应当加强对销售人员的日常管理，建立管理档案，对销售人员行为、诚信、奖惩等方面进行记录。")]),_c('p',{staticClass:"indent"},[_vm._v("第十一条【保密】基金募集机构及其销售人员应当在对履行投资者适当性管理职责过程中获取的投资者信息、投资者风险承受能力评价结果等信息和资料严格保密，防止该等信息和资料泄露或被不当利用。")]),_c('p',{staticClass:"indent"},[_vm._v("第十二条【回访】基金募集机构应当建立健全普通投资者回访制度，对购买基金产品或者服务的普通投资者定期抽取一定比例进行回访，对持有高风险等级基金产品或者服务的普通投资者应当增加回访比例和频次。")]),_c('p',{staticClass:"indent"},[_vm._v("基金募集机构应当对回访时发现的异常情况进行持续跟踪，对异常情况进行核实，存在风险隐患的应当及时排查，并定期整理总结，以完善投资者适当性制度。")]),_c('p',{staticClass:"indent"},[_vm._v("第十三条【回访】回访内容应当包括但不限于以下信息：")]),_c('p',{staticClass:"indent"},[_vm._v("（一）进一步核实普通投资者身份，确认其购买的基金产品或者服务的信息；")]),_c('p',{staticClass:"indent"},[_vm._v("（二）确认普通投资者是否已充分了解所持有基金产品或者服务的特性和风险，是否了解自身风险承受能力，是否了解匹配情况；")]),_c('p',{staticClass:"indent"},[_vm._v("（三）了解销售人员是否在销售环节充分告知普通投资者持有基金产品或者服务的详细信息及风险；")]),_c('p',{staticClass:"indent"},[_vm._v("（四）确认普通投资者是否知悉承担的主要费用、费率及重要权利；")]),_c('p',{staticClass:"indent"},[_vm._v("（五）确认普通投资者是否知悉可能承担的投资损失；")]),_c('p',{staticClass:"indent"},[_vm._v("（六）确认普通投资者是否知悉投诉渠道。 对购买非公开募集基金的普通投资者，还应确认其是否签署购买产品相关协议和风险揭示书。")]),_c('p',{staticClass:"indent"},[_vm._v("第十四条【投诉】基金募集机构应当建立完备的投资者投诉处理体系，准确记录投资者投诉内容。投资者投诉及处理情况应当留痕并存档，投诉电话应当录音。基金募集机构应当妥善处理因履行投资者适当性职责引起的投资者投诉，及时发现业务风险，完善内控制度。")]),_c('p',{staticClass:"indent"},[_vm._v("第十五条【自查】基金募集机构应当至少每半年开展一次投资者适当性管理自查。自查可以采取现场、非现场及暗访相结合的方式进行，并应当形成自查报告留存。 自查内容应当包括但不限于投资者适当性管理制度建设及落实情况，人员考核及培训情况，投资者投诉处理情况，发现业务风险及时整改情况，以及其他需要报告的事项。")]),_c('p',{staticClass:"indent"},[_vm._v("第十六条【留痕】基金募集机构通过营业网点等现场方式执行普通投资者申请成为专业投资者，向普通投资者销售高风险产品或者服务，调整投资者分类、基金产品或者服务分级以及适当性匹配意见，向普通投资者销售基金产品或者服务前对其进行风险提示的环节应当录音或者录像；通过互联网等非现场方式执行的，基金募集机构应当完善信息管理平台留痕功能，记录投资者确认信息。")]),_c('p',{staticClass:"indent"},[_vm._v("第十七条【档案管理】基金募集机构应当建立完善的档案管理制度，妥善保存投资者适当性管理业务资料。投资者适当性管理制度、投资者信息资料、告知警示投资者资料、录音录像资料、自查报告等应当至少保存 20 年。")]),_c('p',{staticClass:"margins"},[_vm._v("第三章 投资者分类")]),_c('p',{staticClass:"indent"},[_vm._v("第十八条【投资者信息表及风险测评问卷】基金募集机构应当根据自然人投资者、机构投资者、金融机构理财产品的各自特点，向投资者提供具有针对性的投资者信息表。基金募集机构应当设计风险测评问卷，并对普通投资者进行风险测评。")]),_c('p',{staticClass:"indent"},[_vm._v("第十九条【投资者分类】投资者分为专业投资者和普通投资者。未对投资者进行分类的，应当履行普通投资者适当性义务。")]),_c('p',{staticClass:"indent"},[_vm._v("第二十条【了解投资者信息的具体内容】了解投资者信息应当包含但不限于《办法》第六条所规定的内容。个人投资者还应当提供有效身份证件、出生日期、性别等信息。因遗产继承等原因申请开户的未成年人，还须提供被继承人死亡证明、有效亲属关系证明、经公证有效的遗嘱或遗赠扶养协议、监护人的身份信息及监护关系证明等文件。年满 16 周岁未满 18 岁、以自己的劳动收入为主要生活来源的自然人，还须提供相关任职证明、收入证明或劳动关系证明。")]),_c('p',{staticClass:"indent"},[_vm._v("《办法》第八条第一款所述机构作为投资者的，还应当向基金募集机构提供营业执照、开展金融相关业务资格证明、机构负责人或者法定代表人信息、经办人身份信息等资料。《办法》第八条第二款所述产品作为投资者的，应当向基金募集机构提供产品成立、备案证明文件等资料及参照金融机构要求提交该产品管理人的机构信息。")]),_c('p',{staticClass:"indent"},[_vm._v("基金募集机构应当告知投资者对其所填资料的真实性、有效性、完整性负责。基金募集机构有合理依据认为投资者不按照规定提供相关信息，提供的信息不真实、不准确、不完整的，应当告知投资者将依法自行承担相应法律责任，并拒绝向其销售基金产品或者服务。")]),_c('p',{staticClass:"indent"},[_vm._v("第二十一条【了解投资者信息的操作程序】基金募集机构应当在为投资者开立账户时，以纸质或者电子文档的形式，向投资者提供信息表，要求其填写相关信息，并遵循以下程序：")]),_c('p',{staticClass:"indent"},[_vm._v("（一）基金募集机构应当执行对投资者的身份认证程序，核查投资者的投资资格，切实履行反洗钱等法律义务。")]),_c('p',{staticClass:"indent"},[_vm._v("（二）基金募集机构应当根据投资者的主体不同，提供相应的投资者信息表。")]),_c('p',{staticClass:"indent"},[_vm._v("（三）基金募集机构应当核实自然人投资者本人或者代表金融机构及其产品的工作人员身份，并要求其如实填写投资者信息表。")]),_c('p',{staticClass:"indent"},[_vm._v("（四）基金募集机构应当对投资者身份信息进行核实，并在核实工作结束之日起 5 个工作日内，将结果以及投资者类型告知投资者。")]),_c('p',{staticClass:"indent"},[_vm._v("第二十二条【认定专业投资者的具体标准】符合《办法》第八条要求的投资者为专业投资者。")]),_c('p',{staticClass:"indent"},[_vm._v("第二十三条【认定专业投资者的工作要求】基金募集机构应当根据《办法》第八条规定，结合投资者信息表内容，对专业投资者资格进行认定。")]),_c('p',{staticClass:"indent"},[_vm._v("第二十四条【对专业投资者细化分类管理的具体标准】")]),_c('p',{staticClass:"indent"},[_vm._v("基金募集机构可以根据专业投资者的业务资格、投资实力、投资经历等因素，对专业投资者进行细化分类和管理。")]),_c('p',{staticClass:"indent"},[_vm._v("基金募集机构对专业投资者进行细化分类的，应当通过适当的方式将分类方法、标准、程序等信息公示。")]),_c('p',{staticClass:"indent"},[_vm._v("第二十五条【对专业投资者细化分类管理的工作要求】基金募集机构对专业投资者进行细化分类的，应当向投资者提供风险测评问卷，对专业投资者的投资知识、投资经验、风险偏好进行评估，并得出相对应的风险等级。")]),_c('p',{staticClass:"indent"},[_vm._v("第二十六条【对普通投资者细化分类管理的具体标准】")]),_c('p',{staticClass:"indent"},[_vm._v("专业投资者之外的，符合法律、法规要求，可以从事基金交易活动的投资者为普通投资者。 基金募集机构应当按照风险承受能力，将普通投资者由低到高至少分为 C1（含风险承受能力最低类别） 、C2、C3、C4、C5 五种类型。")]),_c('p',{staticClass:"indent"},[_vm._v("基金募集机构应当通过适当的方式将分类方法、标准、程序等信息公示。")]),_c('p',[_vm._v("第二十七条【填写风险评估问卷程序】基金募集机构应当向普通投资者以纸质或者电子文档形式提供风险测评问卷，对其风险承受能力进行测试，并遵循以下程序：")]),_c('p',{staticClass:"indent"},[_vm._v("（一）基金募集机构应当核实参加风险测评的投资者或机构经办人员的身份信息。")]),_c('p',{staticClass:"indent"},[_vm._v("（二）基金募集机构以及工作人员测试过程中，不得有提示、暗示、诱导、误导、帮助等行为对测试人员进行干扰，影响测试结果。")]),_c('p',{staticClass:"indent"},[_vm._v("（三）风险测评问卷应当在填写完毕后 5 个工作日内，得出相应结果。")]),_c('p',[_vm._v("第二十八条【对普通投资者细化分类管理的工作要求】基金募集机构应当根据投资者信息表、风险测评问卷以及其它相关材料，对普通投资者风险等级进行综合评估，并在评估工作结束之日起 5 个工作日内，告知投资者风险等级评估结果。 普通投资者拒绝填写风险评估问卷的，基金募集机构应当拒绝向其销售基金产品或者服务。")]),_c('p',[_vm._v("第二十九条【风险承受能力最低类别投资者】风险承受能力最低类别投资者，是指经过基金募集机构评估为 C1 型且符合下列情形之一的普通投资者：")]),_c('p',{staticClass:"indent"},[_vm._v("（一）不具有完全民事行为能力；")]),_c('p',{staticClass:"indent"},[_vm._v("（二）不具备证券期货投资知识或者金融投资经验；")]),_c('p',{staticClass:"indent"},[_vm._v("（三）仅追求稳健收益或者表现出极低的风险容忍程度；")]),_c('p',{staticClass:"indent"},[_vm._v("（四）中国证监会、基金业协会或者基金募集机构认定的其他情形。")]),_c('p',[_vm._v("第三十条【投资者转化的工作要求】符合《办法》第十一条规定的专业投资者、普通投资者可以进行转化。 投资者转化效力范围仅适用于所告知、申请的基金募集机构。其它基金募集机构不得以此作为参考依据，将投资者自行转化。"