产品或者服务。")]),_c('p',{staticClass:"indent"},[_vm._v("第四十二条【划分原则】基金募集机构评估基金产品或者服务的风险等级不得低于协会指定的基金产品或者服务风险等级名录规定的风险等级。涉及投资组合的基金产品或者服务，应当按照基金产品或者服务整体风险等级进行评估。")]),_c('p',{staticClass:"indent"},[_vm._v("第四十三条【划分方法】基金募集机构可以通过定量和定性相结合的方法对基金产品或者服务进行风险分级。 基金募集机构可以根据基金产品或者服务风险因素与风险等级的相关性，确定各项评估因素的分值和权重，建立评估分值与基金产品风险等级的对应关系。")]),_c('p',{staticClass:"indent"},[_vm._v("基金募集机构通过定量分析对基金产品进行风险分级时，可以运用贝塔系数、标准差、风险在险值等风险指标体系，划分基金的期限风险、流动性风险、波动性风险等。")]),_c('p',{staticClass:"margins"},[_vm._v("第五章 普通投资者与基金产品或者服务的风险匹配")]),_c('p',{staticClass:"indent"},[_vm._v("第四十四条【匹配制度】基金募集机构应当制定普通投资者和基金产品或者服务匹配的方法、流程，明确各个岗位在执行投资者适当性管理过程中的职责。 匹配方法至少应当在普通投资者的风险承受能力类型和基金产品或者服务的风险等级之间建立合理的对应关系，")]),_c('p',{staticClass:"indent"},[_vm._v("同时在建立对应关系的基础上将基金产品或者服务风险超越普通投资者风险承受能力的情况定义为风险不匹配。")]),_c('p',{staticClass:"indent"},[_vm._v("第四十五条【匹配原则】基金募集机构应当根据普通投资者风险承受能力和基金产品或者服务的风险等级建立以下适当性匹配原则：")]),_c('p',{staticClass:"indent"},[_vm._v("（一）C1 型（含最低风险承受能力类别）普通投资者可以购买 R1 级基金产品或者服务；")]),_c('p',{staticClass:"indent"},[_vm._v("（二）C2 型普通投资者可以购买 R2 级及以下风险等级的基金产品或者服务；")]),_c('p',{staticClass:"indent"},[_vm._v("（三）C3 型普通投资者可以购买 R3 级及以下风险等级的基金产品或者服务；")]),_c('p',{staticClass:"indent"},[_vm._v("（四）C4 型普通投资者可以购买 R4 级及以下风险等级的基金产品或者服务；")]),_c('p',{staticClass:"indent"},[_vm._v("（五）C5 型普通投资者可以购买所有风险等级的基金产品或者服务。")]),_c('p',{staticClass:"indent"},[_vm._v("第四十六条【禁止行为】基金募集机构向投资者销售基金产品或者服务时，禁止出现以下行为：")]),_c('p',{staticClass:"indent"},[_vm._v("（一）向不符合准入要求的投资者销售基金产品或者服务；")]),_c('p',{staticClass:"indent"},[_vm._v("（二）向投资者就不确定的事项提供确定性的判断，或者告知投资者有可能使其误认为具有确定性的判断；")]),_c('p',{staticClass:"indent"},[_vm._v("（三）向普通投资者主动推介风险等级高于其风险承受能力的基金产品或者服务；")]),_c('p',{staticClass:"indent"},[_vm._v("（四）向普通投资者主动推介不符合其投资目标的基金产品或者服务；")]),_c('p',{staticClass:"indent"},[_vm._v("（五）向风险承受能力最低类别的普通投资者销售风险等级高于其风险承受能力的基金产品或者服务；")]),_c('p',{staticClass:"indent"},[_vm._v("（六）其他违背适当性要求，损害投资者合法权益的行为。")]),_c('p',{staticClass:"indent"},[_vm._v("第四十七条【低风险承受能力投资者购买高风险基金产品的工作要求】最低风险承受能力类别的普通投资者不得购买高于其风险承受能力的基金产品或者服务。 除因遗产继承等特殊原因产生的基金份额转让之外，普通投资者主动购买高于其风险承受能力基金产品或者服务的行为，不得突破相关准入资格的限制。")]),_c('p',{staticClass:"indent"},[_vm._v("第四十八条【销售高风险等级产品】基金募集机构在向普通投资者销售高风险等级的基金产品或者服务时，应向其完整揭示以下事项：")]),_c('p',{staticClass:"indent"},[_vm._v("（一）基金产品或者服务的详细信息、重点特性和风险；")]),_c('p',{staticClass:"indent"},[_vm._v("（二）基金产品或者服务的主要费用、费率及重要权利、信息披露内容、方式及频率")]),_c('p',{staticClass:"indent"},[_vm._v("（三）普通投资者的相关权利，例如投资冷静期和回访确认等程序性安排；")]),_c('p',{staticClass:"indent"},[_vm._v("（四）普通投资者可能承担的损失；")]),_c('p',{staticClass:"indent"},[_vm._v("（五）普通投资者投诉方式及纠纷解决安排。")]),_c('p',{staticClass:"indent"},[_vm._v("第四十九条【主动购买不匹配产品的程序】普通投资者主动要求购买与之风险承受能力不匹配的基金产品或者服务的，基金销售应当遵循以下程序：")]),_c('p',{staticClass:"indent"},[_vm._v("（一）普通投资者主动向基金募集机构提出申请，明确表示要求购买具体的、高于其风险承受能力的基金产品或服务。并同时声明，基金募集机构及工作人员没有在基金销售过程中主动推介该基金产品或服务的信息；")]),_c('p',{staticClass:"indent"},[_vm._v("（二）基金募集机构对普通投资者资格进行审核，确认其不属于风险承受能力最低类别投资者，也没有违反投资者准入性规定；")]),_c('p',{staticClass:"indent"},[_vm._v("（三）基金募集机构向普通投资者以纸质或电子文档的方式进行特别警示，告知其该产品或服务风险高于投资者承受能力；")]),_c('p',{staticClass:"indent"},[_vm._v("（四）普通投资者对该警示进行确认，表示已充分知晓该基金产品或者服务风险高于其承受能力，并明确做出愿意自行承担相应不利结果的意思表示；")]),_c('p',{staticClass:"indent"},[_vm._v("（五）基金募集机构履行特别警示义务后，普通投资者仍坚持购买该产品或者服务的，基金募集机构可以向其销售相关产品或者提供相关服务。")]),_c('p',{staticClass:"indent"},[_vm._v("第五十条【投资者风险承受能力调整】投资者信息发生重大变化的，基金募集机构应当及时更新投资者信息，重新评估投资者风险承受能力，并将调整后的风险承受能力告知投资者。")]),_c('p',{staticClass:"indent"},[_vm._v("第五十一条【基金产品或者服务风险等级调整】基金募集机构销售的基金产品或者服务信息发生变化的，应当及时依据基金产品或者服务风险等级分类名录，重新评估其风险等级。")]),_c('p',{staticClass:"indent"},[_vm._v("第五十二条【匹配调整】由于投资者风险承受能力或基金产品或者服务风险等级发生变化，导致投资者所持有基金产品或者服务不匹配的，基金募集机构应当将不匹配情况告知投资者，并给出新的匹配意见。")]),_c('p',{staticClass:"margins"},[_vm._v("第六章 自律管理")]),_c('p',{staticClass:"indent"},[_vm._v("第五十三条【报告义务】基金募集机构应当在每年度结束之日起三个月内向协会报送投资者适当性制度落实情况的自查报告，以及与投资者适当性相关的投诉情况和处理情况。")]),_c('p',{staticClass:"indent"},[_vm._v("第五十四条【协会职责】协会采取现场检查、非现场检查等方式对基金募集机构建立和执行投资者适当性制度的情况进行定期或者不定期检查，基金募集机构应当予以配合。")]),_c('p',{staticClass:"indent"},[_vm._v("第五十五条【投诉举报】基金管理人、投资者可以按照规定向协会投诉或者举报基金募集机构及其从业人员的违规行为。")]),_c('p',{staticClass:"indent"},[_vm._v("第五十六条【一般违规责任】基金募集机构违反本指引第九条至第十五条、第十七条、第四十九至第五十二条的规定，协会可以要求基金募集机构限期改正。逾期未改正的，协会可以视情节轻重对基金募集机构主要业务负责人采取谈话提醒、书面警示、要求强制参加培训、行业内谴责、加入黑名单等纪律处分。")]),_c('p',{staticClass:"indent"},[_vm._v("第五十七条【严重违规责任】基金募集机构违反本指引第七条、第十六条、第四十六至第四十八条、第五十三条的规定协会可视情节轻重对销售机构采取公开谴责、暂停办理相关业务、取消会员资格等纪律处分；对基金募集机构主要业务负责人，协会可采取加入黑名单、公开谴责、暂停或取消基金从业资格等纪律处分，并加入诚信档案。")]),_c('p',{staticClass:"indent"},[_vm._v("第五十八条【多次违规处分】一年之内基金募集机构两次被要求限期改正，基金募集机构主要负责人两次被采取谈话提醒、书面警示等纪律处分的，协会可对其采取加入黑名单、公开谴责等纪律处分；基金募集机构及其主要业务负责人在两年之内两次被采取加入黑名单、公开谴责等纪律处分的，协会可以取消会员资格，暂停或取消基金募集机构主要业务负责人基金从业资格等纪律处分。")]),_c('p',{staticClass:"indent"},[_vm._v("第五十九条【诚信记录】基金募集机构及其从业人员因违反本指引被协会采取相关纪律处分的，协会可视情节轻重计入诚信档案。")]),_c('p',{staticClass:"indent"},[_vm._v("第六十条【行政与刑事责任】基金募集机构及其从业人员涉嫌违反法律、行政法规、证监会有关规定的，移送证监会或司法机关处理。")]),_c('p',{staticClass:"margins"},[_vm._v("第七章 附则")]),_c('p',{staticClass:"indent"},[_vm._v("第六十一条【适用】在协会备案的证券期货经营机构私募资产管理计划销售适当性管理参考本指引执行；《指引》中未包含的其他类基金产品或者服务，应当由基金募集机构自行参照法律法规制定投资者适当性制度履行投资者适当性义务。")]),_c('p',{staticClass:"indent"},[_vm._v("第六十二条【法条竞合】本指引所规定条款与其它证券期货业自律规则条款内容发生竞合的，在不与《办法》内容、原则、精神、内在逻辑及证监会相关解释相违背的情况下，适用较为严格的规定条款。")]),_c('p',{staticClass:"indent"},[_vm._v("第六十三条【生效】本指引自 2017 年 7 月 1 日起实施。")]),_c('p',{staticClass:"indent"},[_vm._v("第六十四条【解释】本指引由协会负责解释。")])])])}]

