import HDIcon from "@/components/common/Icon";
export default {
  name: 'infoPage',
  props: ["prevUrl", "nextUrl", "first", "last"],
  components: {
    HDIcon: HDIcon
  },
  data: function data() {
    return {};
  },
  methods: {
    prevs: function prevs() {
      if (!this.first) {
        this.$router.push({
          path: this.prevUrl
        });
      }
    },
    nexts: function nexts() {
      if (!this.last) {
        this.$router.push({
          path: this.nextUrl
        });
      }
    }
  }
};